<template>
  <div class="lazyTree h100">
    <div class="edit-tree-toolbar">
      <div>{{ title }}</div>
    </div>
    <div class="edit-tree-content">
      <el-tree
        class="filter-tree"
        highlight-current
        node-key="id"
        :data="data"
        :props="props"
        lazy
        :load="loadNode"
        @node-click="handleNodeClick"
        ref="tree"
      >
      </el-tree>
    </div>
  </div>
</template>
<script>
import {
  queryOrgTree //获取组织信息，还未考虑权限
} from "@/api/common";
export default {
  props: ["haveRemark"],

  data() {
    return {
      indent: 16,
      title: "组织选择",
      resolve: null,
      btnArr: [],
      currentNode: {}, //当前节点
      show: false,
      remarkItem: {
        label: "备注",
        prop: "remark",
        type: "text",
        span: 24
      },
      props: {
        label: "label",
        children: [],
        isLeaf: "isLeaf"
      },
      data: []
    };
  },
  created() {
    this.init();
  },
  watch: {
  },
  methods: {
    currentChange(data, node) {},
    init() {},
    //======================
    //点击事件
    handleNodeClick(node, resolve) {
      this.$emit("handleClickTree", node);
    },
    //刷新懒加载的数据
    refreshLoad() {
      this.loadNode.forEach(item => {
        this.loadChildren(item.node, {}, item.resolve);
      });
    },
    //异步树叶子节点懒加载逻辑
    loadNode(node, resolve) {
      this.resolve = resolve;
      if (node.level === 0) {
        //一级节点处理
        this.requestTree(resolve);
      } else {
        //其余节点处理
        this.getIndex(node.data.id, resolve);
        // this.$emit("handleClickTree", node.data);
      }
    },
    //首次加载一级节点
    requestTree(resolve) {
      queryOrgTree("yhszy").then(res => {
        let data = res.data;
        //对数据进行处理
        this.setTreeData(data, resolve);
      });
    },
    //异步加载叶子节点数据函数
    getIndex(id, resolve) {
      queryOrgTree(id).then(res => {
        let data = res.data;
        //对数据进行处理
        this.setTreeData(data, resolve);
      });
    },
    //获取子节点数据函数
    getClickchild(id, resolve) {
      queryOrgTree(id).then(res => {
        let data = res.data;
        this.setTreeData(data, resolve);
        //对是否具有叶子节点进行处理
      });
    },
    //对数据进行处理并且设置树数据
    setTreeData(data, resolve) {
      if (resolve) {
        let treeData = [];
        data.map(i => {
          let obj = {
            id: i.code,
            label: i.name,
            isLeaf: i.isCatalog ? false : true
          };
          treeData.push(obj);
        });
        resolve(treeData);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.lazyTree {
  border: 1px solid #ebeef5;
  min-height: 300px;
  overflow-y: auto;
  overflow-x: auto;
  margin-bottom: 10px;
}

.edit-tree-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
</style>